@import "./main_style.scss";

#salespage{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;

    

}

.sale-video-container{
    display: flex;
    align-items: center;
    justify-content: center;

    
}

iframe{
    width: 720px;
    height: 405px;
}

.see-catalog-btn{
    background-color: $main-color;
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    font-size: 1rem;
    margin: 1rem;
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 2rem;
    border: none;
    border-radius: 10px;
    padding: 15px;
    background-color: $main-color;
    font-size: 1.2rem;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    transition: background-color 250ms;

    &:hover{
        background-color: #db7900;
    }
}

.form-protector{
    background-color: $main-color;
    border-radius: 20px;
    display: none;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 50%;
    position: fixed;
    top: 35vh;
    box-shadow: 10px 50px 1000px #242424;
    

    input#email, input#name{
        height: 1rem;
        padding: 0.5rem;
        border: none;
        border-radius: 5px;
        width: 90%;

    }

    button#submit{
        background-color:#fff;
        color: #242424;
        height: 1rem;
        font-size: 0.8rem;
        display: flex;
        align-items: center;
        padding: 1rem;
        justify-content: center;
        width: 100%;
        margin: 1rem auto;
    }

    .cancel-btn{
        color: #fff;
        font-weight: bold;
        font-size: 1rem;
        padding: 2rem;
        margin-left: 70%;
        background-color: transparent ;
        border: none;
        cursor: pointer;
    }
}

@media(max-width: 500px){
    .see-catalog-btn{
        font-size: 0.8rem;
        justify-content: center;
        align-items: center;
        font-weight: normal;
    
        &:hover{
            background-color: #db7900;
        }
    }

    .sale-video-container iframe{
        width: 320px;
        height: 190px;
    }
}