
@import 'utilities';
@import 'media';

html{
    scroll-behavior: smooth;
}

body{
    background-color: #fff;
    color: #1f1f1f;
    margin-left: auto;
    margin-right: auto;
}

.separator{
    width: 100%;
    height: 10px;
    background-color: #006eff;

    &.orange{
        background-color: $main-color;
    }
}

.btn-link{
        height: 4rem;
        border: none;
        border-radius: 10px;
        padding: 15px;
        background-color: $main-color;
        font-size: 1.2rem;
        font-weight: bold;
        color: #fff;
        cursor: pointer;
        transition: background-color 250ms;

        &:hover{
            background-color: #db7900;
        }
}


